<template>
  <div class="container contact-form">
    <div class="text-center" style="font-size: 50px">
      <i class="text-dark fas fa-envelope"></i>
    </div>
    <h3 class="text-danger m-0">Contact Us</h3>
    <p class="text-center">
      <i class="text-danger fas fa-map-marker-alt"></i>
      Ste. 220 - 2400 Herodian Way SE Atlanta GA 30080, U.S.A
      <br>
      Phone: +16783600524
      <br>
      Email: info@zoolch.com
    </p>
    <form @submit.prevent="sendEmail" class="pt-1">
      <input type="text" v-model="noBotsAllowed" class="bot-catcher" />

      <div class="row">
        <div class="col-md-6">
          <b-form-group>
            <b-form-input
              v-model="formData.name"
              name="name"
              type="text"
              class="form-control"
              placeholder="Your Name *"
              required
            />
          </b-form-group>
          <b-form-group>
            <b-form-input
              v-model="formData.email"
              name="email"
              type="text"
              class="form-control"
              placeholder="Your Email *"
              required
            />
          </b-form-group>
          <b-form-group>
            <b-form-input
              id="phone"
              v-model="formData.phone"
              name="phone"
              type="text"
              placeholder="Your Phone Number *"
            />
          </b-form-group>

          <b-form-group>
            <button type="submit" class="btnContact">Send Message</button>
          </b-form-group>
        </div>
        <div class="col-md-6">
          <b-form-group>
            <textarea
              v-model="formData.message"
              name="message"
              class="form-control"
              placeholder="Your Message *"
              style="width: 100%; height: 150px"
              required
            ></textarea>
          </b-form-group>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Vue from "vue";
import emailjs from "emailjs-com";

export default Vue.extend({
  data() {
    return {
      noBotsAllowed: null,
      formData: {
        email: "",
        name: "",
        phone: "",
        message: "",
      },
      errorMsg: "",
    };
  },
  methods: {
    contactFormIsValid() {
      return (
        this.formData.email !== "" &&
        this.formData.name !== "" &&
        this.formData.phone !== "" &&
        this.formData.message !== "" &&
        this.noBotsAllowed === null
      );
    },
    sendEmail(e) {
      if (this.contactFormIsValid()) {
        this.errorMsg = "";
        emailjs
          .sendForm(
            "service_2psg7gm",
            "template_1bih0re",
            e.target,
            "user_r3rmKcTp9PA1BXzA5R2cc",
            { ...this.formData }
          )
          .then(
            (result) => {
              // console.log("SUCCESS!", result.status, result.text);
              this.$toast.success("Email sent. We will contact you shortly", {
                position: "top-right",
                message: "Email sent. We will contact you shortly",
              });
            },
            (error) => {
              // console.log("FAILED...", error);
              this.$toast.error(`${error}`, {
                position: "top-right",
                message: error,
              });
            }
          );
      } else {
        this.errorMsg = "Please fill the form with proper data.";
        this.$toast.error(`${this.errorMsg}`, {
          position: "top-right",
          message: this.errorMsg,
        });
      }
      (this.formData.email = ""),
        (this.formData.name = ""),
        (this.formData.phone = ""),
        (this.formData.message = "");
    },
  },
});
</script>

